// app/watchlist/07_WatchlistPage.js
'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import axios from 'axios'; // Import axios
import { useAuth } from '../context/AuthContext';
import Header from '../components/homepage/Header';
import Footer from '../components/homepage/Footer';
import Stock from '../components/dashboard/Stock';

export default function WatchlistPage() {
  const { user } = useAuth();
  const [watchlist, setWatchlist] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [error, setError] = useState(null);

  // --- useEffect Hook for Data Fetching ---
  useEffect(() => {
    // Only fetch data if the user is logged in
    if (user) {
      const fetchWatchlist = async () => {
        setIsLoading(true); // Start loading
        setError(null); // Reset error state

        try {
          // Make GET request to our API endpoint
          const response = await axios.get('/api/watchlist');

          // Check if response contains data and the watchlist array
          if (response.data && Array.isArray(response.data.watchlist)) {
            setWatchlist(response.data.watchlist); // Update state with fetched data
          } else {
             // Handle cases where data format is unexpected
             console.warn("API response missing watchlist array:", response.data);
             setWatchlist([]); // Set to empty array
             // Optionally set an error message here if desired
             // setError("Received invalid data format from server.");
          }
        } catch (err) {
          // Handle errors during the API request (network error, server error)
          console.error("Error fetching watchlist:", err);
          // Set a user-friendly error message
          if (err.response) {
            // The request was made and the server responded with a status code
            // that falls out of the range of 2xx
             setError(`Failed to fetch watchlist. Server responded with ${err.response.status}.`);
          } else if (err.request) {
            // The request was made but no response was received
             setError('Failed to fetch watchlist. No response from server.');
          } else {
            // Something happened in setting up the request that triggered an Error
             setError('Failed to fetch watchlist. Error setting up request.');
          }
           setWatchlist([]); // Clear any potentially stale data
        } finally {
          // This block runs regardless of success or error
          setIsLoading(false); // Stop loading indicator
        }
      };

      fetchWatchlist(); // Call the async function
    } else {
      // If user is not logged in (e.g., logs out while on page), clear data and stop loading.
      // The main 'if (!user)' block will handle rendering the 'Access Denied' message.
      setWatchlist([]);
      setIsLoading(false);
      setError(null);
    }

    // Dependency array: re-run effect if 'user' changes.
  }, [user]);

  // ---- Authentication Check ----
  if (!user) {
    // ... (Access Denied JSX remains the same)
    return (
      <>
        <Header />
        <div className="p-10 text-center min-h-screen">
          <h1 className="text-red-600 text-xl mb-2">Access Denied</h1>
          <p className="mb-4">Please log in to view your watchlist.</p>
          <Link href="/login" legacyBehavior><a className='text-blue-600 underline'>Go to Login</a></Link>
        </div>
        <Footer />
      </>
    );
  }

  // ---- Logged-in User View ----
  // (Conditional rendering JSX remains the same as Step 6)
  return (
    <>
      <Header />
      <main className="p-6 min-h-screen">
        <h1 className="text-xl mb-4">My Watchlist</h1>
        {isLoading ? (
          <p>Loading watchlist...</p>
        ) : error ? (
          <p className="text-red-600">Error: {error}</p>
        ) : watchlist.length === 0 ? (
          <p>Your watchlist is empty.</p>
        ) : (
          <div className="space-y-4">
            {watchlist.map((stockItem, index) => (
              <Stock key={stockItem.name || index} stock={stockItem} />
            ))}
          </div>
        )}
      </main>
      <Footer />
    </>
  );
}